import React from "react";
import styled from "@emotion/styled";
import { Global } from "@emotion/core";
import { Link } from "gatsby";

import GitHub from "./icons/GitHub";
import HeadShot from "./HeadShot";
import SEO from "./SEO";
import Twitter from "./icons/Twitter";
import { color, fontSize, global, space } from "../styles";

import "prismjs/themes/prism-solarizedlight.css";

const Layout = ({
  children,
  header = <Header />,
  pageContext: { frontmatter },
}) => {
  return (
    <>
      <Global styles={global} />
      <SEO {...frontmatter} />

      {header}

      <main>{children}</main>

      <footer
        css={{
          color: color.gray[0],
          fontSize: fontSize.sm,
          marginBottom: space[4],
          marginTop: space[2],
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <div>George Song &copy; {new Date().getFullYear()}</div>
        <div css={{ fontSize: fontSize.xl, display: "flex" }}>
          <FooterLink
            href="https://twitter.com/zukefresh"
            aria-label="Visit my Twitter"
          >
            <TouchTarget>
              <Twitter />
            </TouchTarget>
          </FooterLink>

          <FooterLink
            href="https://github.com/gsong/personal-site"
            aria-label="Visit my GitHub"
          >
            <TouchTarget>
              <GitHub />
            </TouchTarget>
          </FooterLink>
        </div>
      </footer>
    </>
  );
};

const imageSize = 50;

const Header = () => (
  <header css={{ margin: `${space[4]} 0 ${space[7]}` }}>
    <h1>
      <Link
        to="/"
        css={{
          alignItems: "center",
          color: color.gray[1],
          display: "flex",
          fontSize: fontSize.lg,

          ":hover": { textDecoration: "none" },
        }}
      >
        <HeadShot
          css={{
            borderRadius: "50%",
            maxWidth: imageSize,
            maxHeight: imageSize,
          }}
        />
        <div css={{ marginLeft: space[3] }}>George Song</div>
      </Link>
    </h1>
  </header>
);

const TouchTarget = styled.div({
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  height: "48px",
  width: "48px",
});

const FooterLink = styled.a({ color: color.gray[0] });

export default Layout;
